<template>
  <div>
    <modify-header></modify-header>
    <div class="icon-img">
      <img src="../city/imgs/touxiang.jpg" alt="头像">
    </div>
    <div class="input-body">
      <div class="input-item">
        联系人姓名
        <input type="text" v-model="name">
      </div>
      <div class="input-item">
        联系人电话
        <input type="text" v-model="phone">
      </div>
      <div class="input-item">
        联系人邮箱
        <input type="email" v-model="email">
      </div>
      <div class="input-confirm">
        <input type="button" value="修改" @click="handleInputSubmit">
      </div>
    </div>
  </div>
</template>

<script>
import ModifyHeader from './component/Header'
import axios from 'axios'
export default {
  name: 'Modify',
  components: {
    ModifyHeader
  },
  data () {
    return {
      name: '',
      phone: '',
      email: ''
    }
  },
  activated () {
    this.name = this.$store.state.user.name
    this.phone = this.$store.state.user.phonenumber
    this.email = this.$store.state.user.email
    // console.log(this.$store.state.user)
  },
  methods: {
    // 提交数据
    handleInputSubmit () {
      if (this.handleInputReg() === true) {
        // 用URLSearchParams来发送x-www-form-urlencoded
        const params = new URLSearchParams()
        params.append('id', this.$store.state.user.id)
        params.append('name', this.name)
        params.append('phonenumber', this.phone)
        params.append('email', this.email)
        axios.post('api/modify', params)
          .then(function (res) {
            const data = res.data
            alert(data.desc)
            // if (res.status === 200) {
            //   this.name = ''
            //   this.phone = ''
            //   this.email = ''
            // }
          })
      } else {
        alert('您所输入的格式有误')
      }
    },
    // 正则判断输入是否正确
    handleInputReg () {
      let regPhone = /^1[0-9]{10}$/
      let regEmail = /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/
      return regPhone.test(this.phone) && regEmail.test(this.email)
    }
  }
}
</script>

<style lang="stylus" scoped>
  .icon-img
    height 1.8rem
    width 1.8rem
    margin .8rem auto 0 auto
    img
      width 100%
      height 100%
  .input-body
    background-color rgba(204,204,204,0.3)
    display flex
    flex-direction column
    height 10rem
    justify-content flex-start
    font-size .3rem
    .input-item
      display flex
      height 1.5rem
      line-height 1.5rem
      padding .1rem
      align-items center
      input
        flex 1
        height .6rem
        border-radius .3rem
        text-indent .2rem
    .input-confirm
      width 1.5rem
      padding .1rem
      margin 0 auto
      background-color white
      text-align center
      border-radius .2rem
      input
        background-color white
</style>



